import React, { PureComponent } from 'react'
import Helmet from 'react-helmet'
import Typed from 'react-typed'
import { Slide } from 'react-slideshow-image'
import GitHubButton from 'react-github-btn'
import Layout from '../layout'
import SEO from '../components/SEO'
import config from '../../data/SiteConfig'
import creatorPic from '../../static/images/creator.webp'
import BagpackerPic from '../../static/images/bp.webp'
import writerPic from '../../static/images/writer.webp'
import engineerPic from '../../static/images/eng.webp'
import { shortenText } from '../utils/text.util'

const sliderMap = [
  {
    image: creatorPic,
    title: 'Creator',
    desc: '',
    alt: 'Creator',
  },
  {
    image: BagpackerPic,
    title: 'Bagpacker',
    desc: '',
    alt: 'Bagpacker',
  },
  {
    image: writerPic,
    title: 'Writer',
    desc: '',
    alt: 'Writer',
  },
  {
    image: engineerPic,
    title: 'Engineer',
    desc: '',
    alt: 'Engineer',
  },
]

const properties = {
  duration: 1800,
  transitionDuration: 400,
  infinite: true,
  arrows: false,
}

export default class Index extends PureComponent {
  state = {
    loading: true,
    error: false,
    data: [],
  }

  componentDidMount() {
    fetch('https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@ashmeetsehgal')
      .then(res => res.json())
      .then(data => data.items.filter(item => item.categories.length > 0))
      .then(dataList =>
        this.setState({
          loading: false,
          data: dataList,
        }),
      )
      .catch(err =>
        this.setState({
          loading: false,
          error: err,
        }),
      )
  }

  render() {
    const { data, loading, error } = this.state
    return (
      <Layout>
        <Helmet title={`${config.siteTitle} – Full Stack Software Developer`} />
        <SEO />
        <div className="container">
          <div className="lead">
            <div className="newsletter-section">
              <div className="slide-container">
                <Slide {...properties}>
                  {sliderMap.map(item => (
                    <div className="each-fade" key={item.alt}>
                      <div className="image-container">
                        <img src={item.image} className="newsletter-avatar" alt={item.alt} />
                      </div>
                    </div>
                  ))}
                </Slide>
              </div>
              <div>
                <h3>
                  <Typed
                    strings={sliderMap.map(item => item.title)}
                    typeSpeed={80}
                    backDelay={1100}
                    backSpeed={20}
                    loop
                  />
                </h3>
                <p>
                  I write/post stuff on different platforms. Get an update when something new comes
                  out by signing up below!
                </p>
                <a className="button" href="https://ashmeet.substack.com/">
                  Subscribe
                </a>
              </div>
            </div>
            <div className="elevator">
              <h1>Hey 👋 I&apos;m Ashmeet </h1>
              <p>
                I&apos;m a full stack software developer who loves building experiences, improving
                productivity, sharing ideas, solving problems, implementing solutions.
              </p>
              <div className="social-buttons">
                <GitHubButton
                  href="https://github.com/ashmeetsehgal"
                  data-size="large"
                  data-show-count="true"
                >
                  ashmeetsehgal
                </GitHubButton>
              </div>
            </div>
          </div>
        </div>

        <div className="container front-page" />
        {!loading && !error && (
          <div className="gradient-section">
            <div className="container">
              <h2>Tech Posts (More coming soon...)</h2>
            </div>
            <div className="quotations">
              {data &&
                data.map(item => (
                  <a key={item.guid} href={item.link} target="_blank" rel="noopener noreferrer">
                    <blockquote className="quotation">
                      <img src={item.thumbnail} className="medium-img" alt={item.title} />
                      <p>{item.title}</p>
                      <cite>— {shortenText(item.pubDate, 0, 10)}</cite>
                    </blockquote>
                  </a>
                ))}
            </div>
          </div>
        )}
      </Layout>
    )
  }
}
